    -webkit-align-items:center;
            align-items:center;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}
.about h1{
    color:  hsl(0, 0%, 100%);
    font-weight: 600;
    letter-spacing: 1px;
    font-size: 2.5rem;
    margin-bottom: 3rem;
}
.about h2{
