}
.about h4{
    font-size: 1.7rem;
    color: hsl(233, 26%, 24%);
    margin: 2rem 0 1rem 0;
    transition: all 0.2s;
}
.about h4:hover{
    color:hsl(136, 65%, 51%);
}
.about  a{
    color: white;
    letter-spacing: 1px;
    text-decoration: none;

  }
.about .section__features{
    padding:7rem 2.5rem;
    background-color: hsl(0, 0%, 100%) ;
}
.about .section-1{
    width:100%;
    height:43vw;
    min-height: 30vh;
    padding-top: 5vw;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
}
.about .Green_Button{
    box-shadow:2px 2px 2px gray;
    z-index: 2;
    font-weight:500;
}
.about .section__features p{
    font-size: 23px;
    color: #9698a6;
    font-weight: normal;
    margin: 30px 0;
    word-spacing: 1.2px;
}
.about .grid__layout{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-bottom: 5vw;
    width:100%;
    -ms-scroll-snap-type: y mandatory;
        scroll-snap-type: y mandatory;
    overflow-y: scroll;
}
/* Flip card starts */
.about .feature__card{
    scroll-snap-align: center;
    width: 300px;
    height: 380px;
    box-shadow: 0.2rem 0.2rem 2rem rgb(209, 209, 209);
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;

}
.about .flip-card{
    width: 300px;
    height: 300px;
    -webkit-perspective: 1000px;
            perspective: 1000px;
    object-fit: cover;
    }
    .about .flip-card-inner {
    position: relative;
    width: 300px;
    height: 300px;
    text-align: center;
    transition: -webkit-transform 0.7s;
    transition: transform 0.7s;
    transition: transform 0.7s, -webkit-transform 0.7s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-backface-visibility: hidden;
    border:none;
    backface-visibility: hidden;  }
    .about .flip-card:hover .flip-card-inner {
      -webkit-transform: rotateY(180deg);
              transform: rotateY(180deg);
    }
    .about .flip-card:hover .feature__card{
      -webkit-flex-direction: column-reverse;
              flex-direction: column-reverse;
    }
  .about .flip-card-front, .flip-card-back {
    position: absolute;
    width: 300px;
    height: 300px;
    text-align: left;

}
  .about .flip-card-front {
      background-color: rgb(255, 255, 255);
      color: black;
    }

    .about .flip-card-back {
    padding:2vw;
    padding-top: 0vw;
    background-color: #ffffff;
    color: rgba(112, 112, 112, 0.946);
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
/* Flip Card Ends */
.about .section__cards{
    background-color:  hsl(0, 0%, 98%);
    padding: 8rem 12rem;
}
.about .section__cards h3{
    margin-bottom: 6rem;
}
.about figure{
    overflow: hidden;
    background-color: white;
}

.about figure img{
    width: 100%;
}
.about figcaption{
    padding: 2rem 3rem;
    color: hsl(233, 8%, 62%);
}
.about figcaption p{
    font-weight: 700;
    font-size: 1.2rem;
    letter-spacing: 1px;
    line-height: 1.6;
}
@media only screen and (max-width: 1470px){
    .about .grid__layout{
        display: grid;
        grid-template-columns: 50% 50%;
        padding-left: 5vw;
        grid-row-gap:10vw;
        row-gap:10vw;
        grid-column-gap: 5vw;
        -webkit-column-gap: 5vw;
                column-gap: 5vw;
    }
    .about .feature__card{
        -webkit-flex-direction: column-reverse;
                flex-direction: column-reverse;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        padding-top: 1.2vw;
    }
}
