* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'IBM Plex Sans', sans-serif;
}


.main-container {
  padding-top: 35px;
  /* background-color: rgb(246, 246, 246); */
}

h1.main-heading {
  /* text-align: center; */
  font-weight: 500;
  font-size: 2.7rem;
  /* color: #2d314d; */
